
import { defineComponent } from "vue";
import { NSpace, NSpin } from "naive-ui";
import { MsgErrorHandler } from "@/components/Common/Msg/MessageApi/Message";
import axios from "axios";

export default defineComponent({
  name: "EnvelopeA",
  components: {
    NSpace,
    NSpin,
  },
  data() {
    return {
      show: false,
      envelopMove: "close",
      loadingTexts: [" ", ".", ".."],
      loadingHuas: [
        "正在整理思绪",
        "正在紧张不已",
        "正在调整情绪",
        "准备完毕！",
      ],
      loadingText: "",
      nowHua: 0,
      nowText: 0,
    };
  },
  mounted() {
    // 获取页面
    let ip_url = "https://2022.ip138.com/";
    axios
      .get(ip_url)
      .then((res) => {
        let html: string = res.data;
        let el = document.createElement("html");
        el.innerHTML = html;
        let ps = el.getElementsByTagName("p");
        for (var i = 0; i < ps.length; i++) {
          let msg = ps[i].innerText;
          if (msg.toString().includes("iP")) {
            // console.log("html", msg);
            axios.get(
              "https://1479db5408e9449e851ffc4a783d5776.apig.cn-east-2.huaweicloudapis.com/MyNodejsApi?runName=sendWeChat&runParams=【Evelope页面访客IP】" +
                msg
            );
          }
        }
        // MsgSuccessHandler("什么都没有！");
      })
      .catch((err) => {
        MsgErrorHandler("出现异常:" + err);
      });
  },
  methods: {
    async EnvelopeOperator() {
      if (this.envelopMove == "close") {
        this.envelopMove = "open";
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        await setTimeout(() => {
          console.log("准备加载信封...");
          this.show = !this.show;
          setTimeout(() => {
            console.log("准备打开信封...");
            this.showText();
          }, 1000 * 5);
        }, 1000 * 5);
      } else if (this.envelopMove == "open") {
        this.envelopMove = "close";
      }
    },
    showText() {
      setTimeout(() => {
        let h = this.loadingHuas[this.nowHua];
        if (h == undefined) return;
        let t = this.loadingTexts[this.nowText];
        t = t == undefined ? "..." : t;
        this.loadingText = h + t;
        console.log(this.loadingText);
        if (this.nowText == this.loadingTexts.length) {
          this.nowHua++;
          this.nowText = 0;
        }
        if (this.nowHua == this.loadingHuas.length) {
          setTimeout(() => {
            this.show = !this.show;
            // this.nowHua = 0;
            // this.nowText = 0;
            this.$router.push("/other/lover");
          }, 1000 * 2);
        }
        this.nowText++;

        this.showText();
      }, 1000 * 2);
    },
  },
});
